IACvB,sBAAsB;;IAEtB,YAAY;IACZ,kBAAkB;;IAElB,sBAAsB;AAC1B;;AAEA;IACI,4BAA4B;IAC5B,uBAAuB;;IAEvB,kBAAkB;;IAElB,uCAAuC;IACvC,uBAAuB;IACvB,sBAAsB;;IAEtB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,uBAAuB;;IAEvB,kBAAkB;IAClB,mBAAmB;;IAEnB,uCAAuC;IACvC,uBAAuB;IACvB,sBAAsB;;IAEtB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,yCAAyC;AAC7C","sourcesContent":["/* style.css */\n\n@import url(\"./reset.css\");\n\n/* variables */\n:root {\n    --color-main: forestgreen;\n    --color-sec: white;\n    --spacing: 1rem;\n    --padding: 1rem;\n    --font-family-main: Arial, Helvetica, sans-serif;\n    --corners: calc(0.5 *var(--spacing));\n    \n    /* debugging */\n    * {\n        /* outline: solid red 1px; */\n    }\n}\n\nbody {\n    background-color: var(--color-main);\n    color: white;\n    font-size: 1rem;\n    font-family: var(--font-family-main);\n\n    display: grid;\n    grid-template-columns: 24rem minmax(24rem, auto); \n    grid-template-rows: auto 1fr;\n\n    /* set min-height so side bar can always fill up page vertically */\n    min-height: 100vh;\n}\n\n/* ============================================ Header ============================================ */\n\n#header {\n    grid-column: span 2;\n\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n\n    #title-main {\n        justify-self: start;\n    }\n\n    #demo-btn {\n        padding: calc(0 * var(--padding)) calc(0.5 * var(--padding));\n        border-radius: var(--corners);\n        \n        border: solid 1px var(--color-sec);\n        color: var(--color-sec);\n        background-color: var(--color-main);\n        \n        &:hover {\n            border: solid 1px var(--color-main);\n            color: var(--color-main);\n            background-color: var(--color-sec);\n        }\n    }   \n}\n\n#sidebar,\n.todo-card {\n    border-radius: var(--corners);\n}\n\n/* ============================================ Sidebar ============================================ */\n\n#sidebar {\n    background-color: var(--color-sec);\n    color: var(--color-main);\n\n    margin-left: var(--padding);\n    margin-bottom: var(--padding);\n\n    .title-sec {\n        justify-self: center;\n        margin-bottom: var(--padding);\n    }\n    \n    input, \n    .sidebar-proj-list-name,\n    .del-val-name {\n        font-size: larger;\n    }\n\n    .del-confirm-message {\n        margin-left: calc(0.5 * var(--padding));\n    }\n\n    #projects-list {\n        .sidebar-project-li {\n            width: 100%;\n\n            margin-bottom: calc(0.5 * var(--spacing));\n            \n            .sidebar-proj-list-line {\n                display: grid;\n                grid-template-columns: auto 1fr min-content min-content;\n\n                align-items: center;\n\n                gap: calc(0.25 * var(--padding));\n\n                .sidebar-proj-list-name {\n                    display: -webkit-box; /* Use flexible box model to allow clamping */\n                    -webkit-box-orient: vertical; /* Set the orientation to vertical */\n                    overflow: hidden; /* Hide the overflowed content */\n                    -webkit-line-clamp: 1; /* Limit to 3 lines of text */\n                    text-overflow: ellipsis; /* Add ellipsis when content overflows */\n\n                    &:not(.current-filter):hover {\n                        transform: scale(1.1);\n                        cursor: pointer;\n                    }\n                    \n                    &.current-filter {\n                        font-weight: bold;\n                    }\n                }\n\n                .spacer {\n                    height: 0.5px;\n                    background-color: var(--color-main);\n                }\n            }\n        }\n    }\n\n    /* project-edit-line */\n    #add-project-btn-wrappper + .confirm-cancel-line {\n        margin-left: var(--spacing);\n    }\n    \n    #add-project-btn-wrappper {\n        justify-self: center;\n        /* margin-top: var(--spacing); */\n\n        #add-project-btn {\n            display: grid;\n            align-content: center;\n            justify-content: center;\n\n            background-color: var(--color-main);\n            \n            border: none;\n            --btn-size: calc(2 * var(--spacing));\n            \n            height: var(--btn-size);\n            width: var(--btn-size);\n            \n            border-radius: 50%;\n            \n            /* need shift btn txt */\n            div {\n                max-height: min-content;\n                max-width: min-content;\n\n                font-size: xx-large;\n                color: var(--color-sec);\n            }\n        }\n    }\n}\n\n/* ============================================ Main Content ============================================ */\n\n/* set first childs of todo data line to large font */\n#main-content {\n    padding-top: calc(-1 * var(--padding));\n\n    display: grid;\n    gap: var(--padding);\n    align-content: start;\n\n    #default-todo-card {\n        \n        #default-card-form-wrapper {\n            .new-card-title {\n                font-weight: bold;\n                font-size: larger;\n            }\n\n            #required-elm-note {\n                font-style: italic;\n                font-size: smaller;\n                margin-bottom: calc(0.5 * var(--spacing));\n            }\n                    \n            .required-elm {\n                color: red;\n            }\n        \n            .todo-input-line {\n                margin-bottom: calc(0.5 * var(--spacing));\n            }\n        }\n\n        button#submit-new-todo-button {\n            padding: calc(0 * var(--padding)) calc(0.5 * var(--padding));\n            border-radius: var(--corners);\n\n            border: solid 1px var(--color-main);\n            color: var(--color-main);\n            background-color: var(--color-sec);\n            \n            &:hover {\n                border: solid 1px var(--color-sec);\n                color: var(--color-sec);\n                background-color: var(--color-main);\n            }\n        }\n\n        button.add-sign {\n            margin: 0 auto;\n\n            display: grid;\n            align-content: center;\n            justify-content: center;\n\n            background-color: var(--color-main);\n            \n            border: none;\n            --btn-size: calc(2 * var(--spacing));\n            \n            height: var(--btn-size);\n            width: var(--btn-size);\n            \n            border-radius: 50%;\n            \n            /* need shift btn txt */\n            div {\n                max-height: min-content;\n                max-width: min-content;\n\n                font-size: xx-large;\n                color: var(--color-sec);\n            }\n        }\n\n        .default-todo-show-hide-details-btn-wrapper {\n            width: 100%;\n            display: flex;\n            justify-content: center;\n        }\n    }\n\n    .todo-card {\n        background-color: var(--color-sec);\n        color: var(--color-main);\n        \n        max-height: max-content;\n        \n        padding: var(--padding);\n        &:not(#default-todo-card) {\n            padding-bottom: calc(0.5 * var(--padding));\n        }\n        \n        .todo-data-line-wrapper {\n            margin-bottom: calc(0.25 * var(--spacing));\n            max-width: 100%; \n            \n            * {\n                overflow: hidden;\n                text-overflow: ellipsis; /* Add ellipsis when content overflows */\n            }\n\n            .todo-data-line {\n                display: grid;\n                grid-template-columns: auto auto;\n                justify-content: start;\n                \n                .edit-btn {\n                    align-self: start;\n                    margin-top: calc(0.2 * var(--spacing));\n                    margin-right: calc(0.5 * var(--spacing));\n                }\n                \n                &.todo-data-line-title {\n                    .todo-title {\n                        font-size: x-large;\n                        font-weight: bold;\n                    }\n                 \n                    .edit-btn {\n                        margin-top: calc(0.55 * var(--spacing));\n                    }\n                }\n            }\n        }\n            \n        .todo-card-btns-wrapper {\n            display: grid;\n            grid-template-columns: 1fr auto 1fr;\n\n            .todo-del-btn-wrapper {\n                margin-left: auto;\n            }\n        }\n    }\n\n    .todo-card.high-priority {\n        background-color: firebrick;\n        color: var(--color-sec);\n    }\n\n    .todo-card.med-priority {\n        background-color: rgb(238, 131, 0);\n        color: var(--color-sec);\n    }\n\n    .todo-card.low-priority {\n        background-color: rgb(255, 225, 58);\n    }\n\n    .todo-card.done-priority {\n        background-color: lightgreen;\n    }\n\n    /* def-todo-card non-detailed display */\n    .def-todo-card-abridged {\n        #default-card-form-wrapper {\n            display: none;\n        }\n    }\n}\n\n/* ============================================ Catch All ============================================ */\n\n.confirm-cancel-line {\n    max-height: 10px;\n\n    input,\n    select,\n    textarea,\n    .todo-priority-input {\n        margin-right: calc(0.5 * var(--spacing));\n    }\n\n    button:not(.todo-priority-input) {\n        margin-left:  calc(0.25 * var(--padding));\n    }\n}\n\n.error-message {\n    font-style: italic;\n    font-size: small;\n    color: red;\n}\n\n.todo-card.high-priority .error-message,\n.todo-card.med-priority .error-message {\n    color: yellow;\n}\n\n\n/* special case error message, this could be needed for all edit lines */\n#add-project-btn-wrappper + .confirm-cancel-line .error-message {\n    margin-left: var(--spacing);\n}\n\n.del-confirm-message {\n    font-style: italic;\n}\n\n.del-val-name {\n    text-decoration: line-through;\n}\n\ninput {\n    padding-left: calc(0.5 * var(--padding));\n    padding-right: calc(0.5 * var(--padding));\n\n    border: none;\n    outline: 1px grey solid;\n\n    border-radius: var(--corners);\n\n    &:hover,\n    &:focus {\n        outline: solid 2px black;\n    }\n}\n\nselect {\n    padding: calc(0 * var(--padding)) calc(0.5 * var(--padding));\n    border-radius: var(--corners);\n\n    max-width: 7rem;\n\n    &:hover,\n    &:focus {\n        outline: solid 1px black;\n    }\n}\n\ntextarea {\n    padding-left: calc(0.5 * var(--padding));\n    padding-right: calc(0.5 * var(--padding));\n    margin-bottom: calc(-0.25 * var(--padding));\n    \n    border: none;\n    outline: 1px grey solid;\n    \n    border-radius: var(--corners);\n    \n    &:hover,\n    &:focus {\n        outline: solid 2px black;\n    }\n}\n\n/* ===buttons=== */\nbutton {\n    border: none;\n    \n    &:hover {\n        transform: scale(1.1);\n        cursor: pointer;\n        /* border: black solid 1px; */\n    }\n}\n\nbutton.todo-priority-input {\n    padding: calc(0 * var(--padding)) calc(0.5 * var(--padding));\n    border-radius: var(--corners);\n}\n    \nbutton.todo-priority-input.high-priority {\n    color: var(--color-sec);\n    background-color: red;\n    border: solid white 1px;\n}\n\nbutton.done-priority {\n    color: var(--color-main);\n    background-color: greenyellow;\n    border: solid var(--color-main) 1px;\n\n    &#todo-priority-input{\n        border-color: var(--color-sec);\n    }\n}\n\nbutton.low-priority {\n    color: var(--color-main);\n    background-color: yellow;\n    border: solid var(--color-main) 1px;\n    \n    &#todo-priority-input{\n        border-color: var(--color-sec);\n    }\n}\n\nbutton.med-priority {\n    color: var(--color-sec);\n    background-color: orange;\n    border: solid var(--color-sec) 1px;\n}\n\n.show-hide-details-btn {\n    background-color: transparent;\n    \n    &:not(.add-sign) div {\n        max-height: min-content;\n        max-width: min-content;\n    }\n    \n    &.up-arrow div{\n        color: var(--color-main);\n        border-bottom: 1px solid var(--color-main);        \n    }\n    \n    &.down-arrow div{\n        color: var(--color-main);\n        border-top: 1px solid var(--color-main);        \n        margin-bottom: calc(-0.5 * var(--spacing));\n    }\n}\n\n/* up arrow on todo card with high/med priority */\n.todo-card.high-priority .show-hide-details-btn.up-arrow div,\n.todo-card.med-priority .show-hide-details-btn.up-arrow div {\n    border: none;\n    color: var(--color-sec);\n    border-bottom: 1px solid var(--color-sec);\n}\n\n/* down arrow on todo card with high/med priority */\n.todo-card.high-priority .show-hide-details-btn.down-arrow div,\n.todo-card.med-priority .show-hide-details-btn.down-arrow div {\n    border: none;\n    color: var(--color-sec);\n    border-top: 1px solid var(--color-sec);\n}\n\n.confirm-btn {\n    background-color: var(--color-main);\n    color: var(--color-sec);\n    \n    font-size: smaller;\n    font-weight: bold;\n    \n    --btn-size: calc(1.25 * var(--spacing));\n    height: var(--btn-size);\n    width: var(--btn-size);\n    \n    border: none;\n    border-radius: 50%;\n}\n\n.cancel-btn {\n    background-color: dodgerblue;\n    color: var(--color-sec);\n    \n    font-size: smaller;\n    font-weight: bolder;\n    \n    --btn-size: calc(1.25 * var(--spacing));\n    height: var(--btn-size);\n    width: var(--btn-size);\n    \n    border: none;\n    border-radius: 50%;\n\n    transform: scale(1.05);\n}\n\n.edit-btn {\n    background-color: dodgerblue;\n    color: var(--color-sec);\n    \n    font-size: smaller;\n    \n    --btn-size: calc(1.25 * var(--spacing));\n    height: var(--btn-size);\n    width: var(--btn-size);\n    \n    border: none;\n    border-radius: 50%;\n}\n\n.del-btn {\n    background-color: red;\n    color: var(--color-sec);\n    \n    font-size: smaller;\n    font-weight: bolder;\n    \n    --btn-size: calc(1.25 * var(--spacing));\n    height: var(--btn-size);\n    width: var(--btn-size);\n    \n    border: none;\n    border-radius: 50%;\n}\n\n.todo-card .del-btn {\n    margin-right: calc(-0.5 * var(--spacing));\n}"],"sourceRoot":""}]);
